<script>
import axios from 'axios';
import ArgonButton from "@/components/ArgonButton.vue";
import ArgonInput from "@/components/ArgonInput.vue";

export default {
    name:'companyTable',
    components:{
      ArgonButton,
      ArgonInput
    },
    data(){
        return{
            allEntreprise:[],
            checkedEntreprise:[],
            libelleList:"",
            allList:[],
            selectedList:0,
            searchString:"",
            searchCriterea:"nom_entreprise",
            capital_inf:"",
            capital_sup:"",
            pageSize:10,
            current: 1,
        }
    },
    computed:{
      indexStart() {
        return (this.current - 1) * this.pageSize;
      },
      indexEnd() {
        return this.indexStart + this.pageSize;
      },
      paginated() {
        if(this.pageSize>this.allEntreprise.length){return this.allEntreprise;}
        else {return this.allEntreprise.slice(this.indexStart, this.indexEnd);}
      },
      pageCount(){
        if(this.pageSize>this.allEntreprise.length){return 1;}
        else {return this.allEntreprise.length/this.pageSize;}
      }

    },


    methods:{
      prev() {
        if(this.current>1){
        this.current--;
        }
      },
    next() {
      if(this.current<this.pageCount){
      this.current++;
      }
    },
    currentPage(pageNum){
      this.current=pageNum
    },


    onlyNumber ($event) {
      //console.log($event.keyCode); //keyCodes value
      let keyCode = ($event.keyCode ? $event.keyCode : $event.which);
      if ((keyCode < 48 || keyCode > 57)) { // 46 is dot
          $event.preventDefault();
      }
    },
        getAllCompany(){
            axios.get('https://prospect-api.konfiant.com/api/getAllEntreprise').then((response)=>{
                console.log(response);
                this.allEntreprise=response.data
            })
            .catch((error)=>{
                console.log(error);
            })
        },
        updateList(){
          axios.post('https://prospect-api.konfiant.com/api/updateList',{elements:this.checkedEntreprise,id_list:this.selectedList})
          .then((response)=>{
            console.log(response);
            this.getAllList();
            this.checkedEntreprise=[];
          })
          .catch((error)=>{
            console.log(error);
          })
        },
        getCompaniesByCapital(){
          
          axios.post('https://prospect-api.konfiant.com/api/searchFilter',{
            critere_recherche:this.searchCriterea,
            valeur_recherche:'test',
            valeur_capital_superieure:this.capital_sup,
            valeur_capital_inferieure:this.capital_inf,}).then((response)=>{
            console.log(response);
            this.allEntreprise=response.data;
          })
          .catch((error)=>{
            console.log(error);
          })

        },
        getAllList(){
        axios.get('https://prospect-api.konfiant.com/api/getAllListEntreprise').then((response)=>{
          console.log(response);
          this.allList=response.data
          this.selectedList=response.data[0].id
        })
        .catch((error)=>{
          console.log(error);
        })
      },
        createList(){
          axios.post('https://prospect-api.konfiant.com/api/createList',{elements:this.checkedEntreprise,libelle_liste:this.libelleList}).then((response)=>{
            console.log(response);
            this.libelleList="";
            this.checkedEntreprise=[];
          })
          .catch((error)=>{
            console.log(error);
          })
        },
        filterSearch(){
          axios.post('https://prospect-api.konfiant.com/api/searchFilter',{critere_recherche:this.searchCriterea,valeur_recherche:this.searchString}).then((response)=>{
            console.log(response);
            this.allEntreprise=response.data;
          })
          .catch((error)=>{
            console.log(error);
          })
        },
        isAuthenticated(){
          axios.defaults.withCredentials = true
        axios.get("https://prospect-api.konfiant.com/api/isAuthenticated").then((response)=>{
      console.log(response);
      
    })
    .catch((error)=>{
      console.log(error);
      this.$router.replace(this.$route.query.redirect || '/signin');
    })
    }
    },

    
    beforeMount(){
        
        this.isAuthenticated();
        this.getAllCompany();
    }
}
</script>
<template>

  <div class="modal fade" id="modificationModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">Choissisez une liste</h1>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Liste :</label
                    >
                    
                    <select class="form-select" aria-label="Default select example" v-model="this.selectedList">
                      <option v-for="list in this.allList" :key="list.id" :value="list.id">{{ list.libelle_liste }}</option>
                      
                    </select>
                  </div>
                  </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
          <button type="button" class="btn btn-primary" @click="updateList()" data-bs-dismiss="modal">Sauvegarder</button>
        </div>
      </div>
    </div>
  </div>


<!-- Modal -->
<div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="exampleModalLabel">Donnez un nom  votre liste</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        <div class="row">
                <div class="col-md-6">
                  <label for="example-text-input" class="form-control-label"
                    >Nom :</label
                  >
                  
                  <argon-input type="text" v-model="this.libelleList" />
                </div>
                </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary" @click="createList()" data-bs-dismiss="modal">Sauvegarder</button>
      </div>
    </div>
  </div>
</div>




  <div class="card">
    <div class="card-header pb-0">
      <h6>Table des entreprises</h6>
    </div>
    <div class="row mx-4 m-2">
      <div class="col-2"><label for="recherche-text-input" class="form-control-label my-2"
        >Filtre de recherche </label
      ></div>
    <div class="col-2"><select class="form-select" aria-label="Default select example" v-model="this.searchCriterea" @change="filterSearch()">
      <option value="ville_entreprise">Ville</option>
      <option value="region">Region</option>
      <option value="nom_entreprise" selected >Nom</option>
      <option value="capital_entreprise">Capital</option>
      <option value="category">Categorie</option>
      <option value="date_creation">Date cration</option>
    </select></div>
    <div class="col-4">
      <argon-input type="date" v-model="this.searchString" @change="filterSearch()" v-if="this.searchCriterea==='date_creation'"/>
      <div class="row" v-else-if="this.searchCriterea==='capital_entreprise'">
        <div class="col"><argon-input type="text" placeholder="Minimum" v-model.number="this.capital_sup" @input="getCompaniesByCapital()" @keypress="onlyNumber" /></div>
      <div class="col"><argon-input type="text" placeholder="Maximum" v-model.number="this.capital_inf" @input="getCompaniesByCapital()" @keypress="onlyNumber" /></div>
    </div>
      <argon-input type="text" v-model="this.searchString" @input="filterSearch()" v-else /></div>
  </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2">

              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Nom</th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Ville</th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Pays</th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Capital</th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2"
              >Catgorie</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="entreprise in paginated" :key="entreprise.id">
              <td>
                <div class="form-check d-flex flex-column justify-content-center">
                <input class="form-check-input" type="checkbox" :value="entreprise.id" id="flexCheckDefault" v-model="checkedEntreprise">
                </div>
              </td>
              <td>
                
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{entreprise.nom_entreprise}}</h6>
                </div>
              </td>
              <td>
                
                <div class="d-flex flex-column justify-content-center">
                  <h6 class="mb-0 text-sm">{{entreprise.ville_entreprise}}</h6>
              </div>
            </td>
            <td>
                
                <div class="d-flex flex-column justify-content-center">
                  <h6 class="mb-0 text-sm">{{entreprise.country}}</h6>
              </div>
            </td>
            <td>
                
                <div class="d-flex flex-column justify-content-center">
                  <h6 class="mb-0 text-sm">{{entreprise.capital_entreprise}}</h6>
              </div>
            </td>
            <td>
                
                <div class="d-flex flex-column justify-content-center">
                  <h6 class="mb-0 text-sm">{{entreprise.category}}</h6>
              </div>
            </td>
            
             
            </tr>
          </tbody>
        </table>


        <nav aria-label="Page navigation-sm">
          <ul class="pagination pagination-sm my-2 mx-2">
            <li class="page-item cursor-pointer" @click="prev">
              <a class="page-link"  aria-label="Previous">
                <span aria-hidden="true">&laquo;</span>
              </a>
            </li>
            <li :class="this.current==index ? 'page-item active mx-1 cursor-pointer' : 'page-item mx-1 cursor-pointer'" v-for="index in pageCount" :key="index" @click="currentPage(index)"><a class='page-link' href="#" disabled>{{index}}</a></li>
            
            <li class="page-item cursor-pointer" @click="next()">
              <a class="page-link"  aria-label="Next" >
                <span aria-hidden="true">&raquo;</span>
              </a>
            </li>
          </ul>
        </nav>


        

        <argon-button color="success" class="ms-3 mx-3 my-3" data-bs-toggle="modal" data-bs-target="#exampleModal" >
           
          Enregistrer  Une nouvelle liste
        </argon-button>
        <argon-button color="info" class="ms-1 mx-1 my-3" data-bs-toggle="modal" data-bs-target="#modificationModal" @click="getAllList()" >
           
            Enregistrer  une liste existente
          </argon-button>
      </div>
    </div>
  </div>
</template>