<script>
import axios from 'axios';
export default {
    name:'facture',
    data(){
        return{
            allFactures:[],
            current: 1,
            pageSize:10,
        }
    },

    computed:{
        indexStart() {
        return (this.current - 1) * this.pageSize;
      },
      indexEnd() {
        return this.indexStart + this.pageSize;
      },
      paginated() {
        if(this.pageSize>this.allFactures.length){return this.allFactures;}
        else {return this.allFactures.slice(this.indexStart, this.indexEnd);}
      },
      pageCount(){
        if(this.pageSize>this.allFactures.length){return 1;}
        else {return this.allFactures.length/this.pageSize;}
      }
    },
    methods:{
        prev() {
        if(this.current>1){
        this.current--;
        }
      },
    next() {
      if(this.current<this.pageCount){
      this.current++;
      }
    },
    currentPage(pageNum){
      this.current=pageNum
    },
        getAllFactures(){
            axios.get('http://127.0.0.1:8000/api/getAllFacture').then((response)=>{
                console.log(response);
                this.allFactures=response.data;
            })
            .catch((error)=>{
                console.log(error);
            })
        }
    },

    mounted(){
        this.getAllFactures();
    }
}
</script>
<template>


    <div class="card w-60 m-auto">
        <div class="card-header pb-0">
          <h6>Table des factures</h6>
        </div>
        
        <div class="card-body px-0 pt-0 pb-2">
          <div class="table-responsive p-0">
            <table class="table align-items-center mb-0">
              <thead>
                <tr>

                  <th
                    class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2 justify-content-center text-center"
                  >Date Facture</th>
                  <th
                    class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2 justify-content-center text-center"
                  >Montant</th>
                  <th
                    class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2 justify-content-center text-center"
                  >Status</th>
                  <th
                    class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7 ps-2 justify-content-center text-center"
                  ><span>Tlchargement</span></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="facture in paginated" :key="facture.id">
                  
                  <td class="justify-content-center text-center">
                    
                      <div class="d-flex flex-column justify-content-center">
                        <h6 class="mb-0 text-sm">{{facture.date_facture}}</h6>
                    </div>
                  </td>
                  <td class="justify-content-center text-center">
                    
                    <div class="d-flex flex-column justify-content-center">
                      <h6 class="mb-0 text-sm">{{facture.montant}}$</h6>
                  </div>
                </td>
                <td class="justify-content-center text-center">
                    
                    <div class="d-flex flex-column justify-content-center">
                      <span v-if="facture.status===1" class="badge bg-success mx-1">Payer</span>
                      <span v-else class="badge bg-danger mx-1">Non Payer</span>
                  </div>
                </td>
                <td class="justify-content-center text-center">
                    
                    <div class="d-flex flex-column justify-content-center">
                      <a :href="'http://127.0.0.1:8000/storage/'+facture.dc_path" target="_blank"><span class="badge bg-info mx-1 cursor-pointer">Tlcharger</span></a>
                  </div>
                </td>
                
                
                 
                </tr>
              </tbody>
            </table>
    
    
            <nav aria-label="Page navigation-sm">
              <ul class="pagination pagination-sm my-2 mx-2">
                <li class="page-item cursor-pointer" @click="prev">
                  <a class="page-link"  aria-label="Previous">
                    <span aria-hidden="true">&laquo;</span>
                  </a>
                </li>
                <li :class="this.current==index ? 'page-item active mx-1 cursor-pointer' : 'page-item mx-1 cursor-pointer'" v-for="index in pageCount" :key="index" @click="currentPage(index)"><a class='page-link' href="#" disabled>{{index}}</a></li>
                
                <li class="page-item cursor-pointer" @click="next()">
                  <a class="page-link"  aria-label="Next" >
                    <span aria-hidden="true">&raquo;</span>
                  </a>
                </li>
              </ul>
            </nav>
            </div></div></div>

</template>