<script>
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import axios from 'axios'
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import ArgonSwitch from "@/components/ArgonSwitch.vue";
export default{
    name:'formulaire',
    components:{
        ArgonInput,
        ArgonButton,
        ArgonSwitch
        
        
    },
    data() {
            return {
                allList:[],
                selectedList:0,
                dateFormulaire:Date,
                nomFormulaire:"",
                confirmMessage:"",
                dcfor:File,
                confirmstatus:0,
                editor: ClassicEditor,
                editorData: 'Delete this ..<br><br><br><br><br>',
                uploadState:false,
                typeSave:0,
                editorConfig: {
                    // The configuration of the editor.
                    
                }
              }
            },
    methods:{
      getAllList(){
        axios.get('https://prospect-api.konfiant.com/api/getAllListEntreprise').then((response)=>{
          console.log(response);
          this.allList=response.data
          this.selectedList=response.data[0].id
        })
        .catch((error)=>{
          console.log(error);
        })
      },
      insererFormulaire(){
        const formData = new FormData();
        if(this.uploadState===true){this.typeSave=1;this.editorData="";
        formData.append("dcformulaire", document.getElementById('formfile').files[0]);}
        else{this.typeSave=0;}

        
        
        formData.append("nom_formulaire",this.nomFormulaire)
        formData.append("date_formulaire",this.dateFormulaire)
        formData.append("contenu_formulaire",this.editorData)
        formData.append("liste_formulaire",this.selectedList)
        formData.append("type_save",this.typeSave)
        axios.post('https://prospect-api.konfiant.com/api/submitFormulaire',formData,{headers: {
      'Content-Type': 'multipart/form-data'
    }}
        ).then((response)=>{
          console.log(response);
          this.dateFormulaire="",
          this.editorData="Delete this ..<br><br><br><br><br>",
          this.nomFormulaire=""
          this.confirmMessage=response.data
          this.confirmstatus=response.status
          document.getElementById('formfile').value=null
        })
        .catch((error)=>{
          console.log(error);
        })
      },
      isAuthenticated(){
          axios.defaults.withCredentials = true
        axios.get("https://prospect-api.konfiant.com/api/isAuthenticated").then((response)=>{
      console.log(response);
      
    })
    .catch((error)=>{
      console.log(error);
      this.$router.replace(this.$route.query.redirect || '/signin');
    })
    },
    onFileChange(e) {
      this.dcfor = e.target.files || e.dataTransfer.files;
      console.log(this.dcfor);
      },
    },
    

    beforeMount(){
      
      this.isAuthenticated();
      this.getAllList();
    }
}

</script>
<template>
    <div class="py-4 container-fluid">
    <div class="card w-90">
        <div class="card-header pb-0">
          <div class="d-flex align-items-center">
          <argon-switch  class="ms-auto" id="rememberMe" @change="this.uploadState=!this.uploadState" >{{ this.uploadState? "Rdiger la lettre" : "Uploader la lettre"}}</argon-switch>
            </div></div>
 
    <h3 class="mx-3">Formulaire d'envoi</h3>
    <div class="alert alert-success w-40 m-auto my-2" v-if="this.confirmstatus===200" role="alert">
      {{ this.confirmMessage }}
    </div>
    <div class="alert alert-danger w-40 m-auto my-2" v-else-if="this.confirmstatus===401" role="alert">
      {{ this.confirmMessage }}
    </div>
    <div class="row mx-3">
      
      <div class="col">
        <label for="example-text-input" class="form-control-label"
          >Nom formulaire</label
        >
        <argon-input type="text" v-model="this.nomFormulaire" />
      </div>
      <div class="col">
        <label for="example-text-input" class="form-control-label"
          >Date formulaire</label
        >
        <argon-input type="date" v-model="this.dateFormulaire" />
      </div>
      </div>
      <div class="row mx-3">
        <div class="col">
          
        <div class="mb-3">
          <label for="formFile" class="form-label">Fichier formulaire</label>
          
          <input class="form-control" type="file" id="formfile" name="formfile" v-on:change="onFileChange" :disabled="this.uploadState ? false : true" accept=".pdf">
        </div>
        </div> 
      <div class="col">
        <label for="example-text-input" class="form-control-label"
          >Liste formulaire</label
        >
        <select class="form-select" aria-label="Default select example" v-model="this.selectedList">
          <option v-for="list in this.allList" :key="list.id" :value="list.id">{{ list.libelle_liste }}</option>
          
        </select>
      </div>
      <div class="col-md-12">
        <ckeditor :editor="editor" v-model="editorData" :config="editorConfig"  :disabled="this.uploadState ? true : false"></ckeditor>
      </div>
    </div>
    
    <div class="mx-4 my-4">
      
    <argon-button color="success" class="ms-auto" @click="this.insererFormulaire()"
                  >Enregistrer Formulaire</argon-button
                >
            </div>
    </div>
    </div>
</template>