<script>
import axios from 'axios'
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonButton from "@/components/ArgonButton.vue";
export default{
    name:'register',
    components:{
        ArgonButton,
        ArgonInput
    },
    data(){
        return{
            user:[],
            confirmStatus:0,
            allTypeEntreprise:[],
            userRole:0

        }
    },
    methods:{
      getAllTypesEntreprise(){
          axios.get('https://prospect-api.konfiant.com/api/getAllTypesEntreprise').then((response)=>{
            console.log(response.data);
            this.allTypeEntreprise=response.data;
            this.user.type_entreprise=response.data[0].id
          })
          .catch((error)=>{
            console.log(error);
          })

        },
      verifyRole(){
        axios.defaults.withCredentials = true
        axios.get('https://prospect-api.konfiant.com/api/getCurrentUserRole').then((response)=>{
          this.userRole=response.data[0].role_user;
          console.log(this.userRole)
          if(this.userRole==0){this.$router.replace(this.$route.query.redirect || '/main');}

        })
        .catch((error)=>{
          console.log(error);
          this.userRole=0;
          if(this.userRole==0){this.$router.replace(this.$route.query.redirect || '/main');}
        })

        
        

      },
    registerUser(){
    axios.post('https://prospect-api.konfiant.com/api/registerUser',{
                name:this.user.name,
                password:this.user.password,
                prenom:this.user.prenom,
                email:this.user.email,
                adresse:this.user.adresse,
                city:this.user.city,
                country:this.user.country,
                zip:this.user.zip,
                entreprise:this.user.entreprise,
                type_entreprise:this.user.type_entreprise,
                siren:this.user.siren,
                phone:this.user.phone
    })
    .then((response)=>{
      console.log(response)
      this.user=[]
      this.confirmStatus=response.status
    })
    .catch((error)=>{
      console.log(error);
    })
  }

    },
    mounted(){

      this.getAllTypesEntreprise()
      
    },
    beforeMount(){
      this.verifyRole();
    }

}
</script>
<template>

    <div class="py-4 container-fluid " v-if="this.userRole===1">
        <div class="row row justify-content-center">
          <div class="col-md-10" style="margin-left:20px">
            <div class="card">
              <div class="card-header pb-0">
                <div class="d-flex align-items-center">
                  <p class="mb-0">Register Profile</p>
                                  
                </div>
              </div>
              <div class="card-body">
                <p class="text-uppercase text-sm">User Information</p>
                <div class="alert alert-success w-40 m-auto my-2" v-if="this.confirmStatus===200" role="alert">
                  Compte crer avec succes
                </div>
                <div class="row">
                  
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Email address</label
                    >
                    <argon-input type="email" v-model="this.user.email" />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Mot de passe</label
                    >
                    <argon-input type="password" placeholder="Password" aria-label="Password" v-model="this.user.password"/>

                  </div>

                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >First name</label
                    >
                    <argon-input type="text" v-model="this.user.prenom" ></argon-input>
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Last name</label
                    >
                    <argon-input type="text" v-model="this.user.name" />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Siren</label
                    >
                    <argon-input type="text" v-model="this.user.siren" />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Numro tlphone</label
                    >
                    <argon-input type="text" v-model="this.user.phone" />
                  </div>
                </div>
                <hr class="horizontal dark" />
                <p class="text-uppercase text-sm">Contact Information</p>
                <div class="row">
                  <div class="col-md-12">
                    <label for="example-text-input" class="form-control-label"
                      >Address</label
                    >
                    <argon-input
                      type="text"
                      v-model="this.user.adresse" 
                    />
                  </div>
                  <div class="col-md-4">
                    <label for="example-text-input" class="form-control-label"
                      >City</label
                    >
                    <argon-input type="text" v-model="this.user.city" />
                  </div>
                  <div class="col-md-4">
                    <label for="example-text-input" class="form-control-label"
                      >Country</label
                    >
                    <argon-input type="text" v-model="this.user.country" />
                  </div>
                  <div class="col-md-4">
                    <label for="example-text-input" class="form-control-label"
                      >Postal code</label
                    >
                    <argon-input type="text" v-model="this.user.zip" />
                  </div>
                </div>
                <hr class="horizontal dark" />
                <p class="text-uppercase text-sm">Info entreprise</p>
                <div class="row">
                    <div class="col-md-6">
                        <label for="example-text-input" class="form-control-label"
                          >Entreprise</label
                        >
                        <argon-input  type="text" v-model="this.user.entreprise" />
                      </div>
                      <div class="col-md-6">
                        <label for="example-text-input" class="form-control-label"
                          >Type entreprise</label
                        >
                        <select class="form-select" aria-label="Default select example" v-model="this.user.type_entreprise">
                          <option v-for="TypeE in this.allTypeEntreprise" :key="TypeE.id" :value="TypeE.id">{{ TypeE.libelle_type }}</option>
                          </select>
                      </div>
                </div>
                <argon-button color="success" size="sm" class="ms-auto"
                    @click="registerUser()">Sauvegarder</argon-button
                  >
              </div>
            </div>
          </div>
          <div class="col-md-4">
            <profile-card />
          </div>
        </div>
      </div>
</template>