import { createRouter, createWebHistory } from "vue-router";
import Tables from "../views/Tables.vue";
import Billing from "../views/Billing.vue";
import VirtualReality from "../views/VirtualReality.vue";
import RTL from "../views/Rtl.vue";
import Profile from "../views/Profile.vue";
import Signup from "../views/Signup.vue";
import Signin from "../views/Signin.vue";
import entreprise from "../views/entreprise.vue";
import formulaire from "../views/formulaire.vue";
import formulaireList from "../views/formulaireList.vue";
import user from "../views/user.vue";
import register from "../views/register.vue";
import facture from "../views/facture.vue";

const routes = [
  {
    path: "/",
    name: "/",
    redirect: "/entreprise",
  },
  {
    path: "/main",
    name: "main",
    component: entreprise,
  },
  {
    path: "/user",
    name: "user",
    component: user,
  },
  {
    path: "/facture",
    name: "Facture",
    component: facture,
  },
  {
    path: "/register",
    name: "register",
    component: register,
  },
  {
    path: "/tables",
    name: "Tables",
    component: Tables,
  },
  {
    path: "/entreprise",
    name: "Entreprise",
    component: entreprise,
  },
  {
    path: "/formulaire",
    name: "Formulaire d'envoi",
    component: formulaire,
  },
  {
  path: "/formulaireList",
  name: "Liste des Formulaires",
  component: formulaireList,
  },
  {
    path: "/billing",
    name: "Billing",
    component: Billing,
  },
  {
    path: "/virtual-reality",
    name: "Virtual Reality",
    component: VirtualReality,
  },
  {
    path: "/rtl-page",
    name: "RTL",
    component: RTL,
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile,
  },
  {
    path: "/signin",
    name: "Signin",
    component: Signin,
  },
  {
    path: "/signup",
    name: "Signup",
    component: Signup,
  },
  
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  linkActiveClass: "active",
});

export default router;
