<script>
import SidenavItem from "./SidenavItem.vue";
import axios from "axios";

export default {
  name: "SidenavList",
  props: {
    cardBg: String
  },
  data() {
    return {
      title: "Argon Dashboard 2",
      controls: "dashboardsExamples",
      isActive: "active",
      userRole:0,
    };
  },
  components: {
    SidenavItem,
  },
  methods: {
    getRoute() {
      const routeArr = this.$route.path.split("/");
      return routeArr[1];
    },
    getRole(){
      axios.defaults.withCredentials = true
      axios.get('https://prospect-api.konfiant.com/api/getCurrentUserRole').then((response)=>{
        this.userRole=response.data[0].role_user
        
      })
      .catch((error)=>{
        console.log(error);
      })

      }
    
  },
  mounted(){
    this.getRole();
  }
};
</script>
