<script>
import axios from 'axios'
import GradientLineChart from "@/examples/Charts/GradientLineChart.vue"
export default{
    name:'formulaireList',
    components:{
        GradientLineChart
    },
    data(){
        return{
            allFormulaire:[],
            formulaireList:[],
            formulaireDocument:"",
            pageSize:10,
            current: 1,
        }
    },
    computed:{
      indexStart() {
        return (this.current - 1) * this.pageSize;
      },
      indexEnd() {
        return this.indexStart + this.pageSize;
      },
      paginated() {
        if(this.pageSize>this.allFormulaire.length){ return this.allFormulaire;}
        else{return this.allFormulaire.slice(this.indexStart, this.indexEnd);}
      },
      pageCount(){
        if(this.pageSize>this.allFormulaire.length){ return 1;}
        else {return this.allFormulaire.length/this.pageSize;}
      }

    },
    methods:{
      prev() {
        if(this.current>1){
        this.current--;
        }
      },
    next() {
      if(this.current<this.pageCount){
      this.current++;
      }
    },
    currentPage(pageNum){
      this.current=pageNum
    },
        getAllFormulaire(){
            axios.get('https://prospect-api.konfiant.com/api/getAllFormulaire').then((response)=>{
                console.log(response);
                this.allFormulaire=response.data
            })
            .catch((error)=>{
                console.log(error);
            })
        },

        getFormulaireList(id){
          axios.post('https://prospect-api.konfiant.com/api/getFormulaireList',{id_liste:id}).then((response)=>{
            console.log(response);
            this.formulaireList=response.data[0].entreprise
          })
          .catch((error)=>{
            console.log(error);
          })
        },
        getFormulaireDocument(id){
          this.formulaireDocument=this.allFormulaire.find(documents=>documents.id===id).contenu_formulaire
        },

        isAuthenticated(){
          axios.defaults.withCredentials = true
        axios.get("https://prospect-api.konfiant.com/api/isAuthenticated").then((response)=>{
      console.log(response);
      
    })
    .catch((error)=>{
      console.log(error);
      this.$router.replace(this.$route.query.redirect || '/signin');
    })
    }

    },
    
    beforeMount(){
      this.isAuthenticated();
      this.getAllFormulaire()
    }
}

</script>

<template>


<!-- Modal -->
<div class="modal fade" id="listModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="exampleModalLabel">Liste entreprise</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">


        <div class="table-responsive p-0">
          <table class="table align-items-center mb-0">
            <tbody>
              <tr v-for="entreprise in this.formulaireList" :key="entreprise.id">
                
                  <td>
                      <div class="text-center">
                        <p class="mb-0 text-xs font-weight-bold">Nom:</p>
                        <h6 class="mb-0 text-sm">{{ entreprise.nom_entreprise }}</h6>
                      </div>
                    </td>
                    <td>
                      <div class="text-center">
                        <p class="mb-0 text-xs font-weight-bold">Ville</p>
                        <h6 class="mb-0 text-sm">{{entreprise.ville_entreprise }}</h6>
                      </div>
                    </td>
                    <td>
                      <div class="text-center">
                        <p class="mb-0 text-xs font-weight-bold">Capital</p>
                        <h6 class="mb-0 text-sm">{{entreprise.capital_entreprise }}</h6>
                      </div>
                    </td>
                    <td>
                      <div class="text-center">
                        <p class="mb-0 text-xs font-weight-bold">Region</p>
                        <h6 class="mb-0 text-sm">{{entreprise.region }}</h6>
                      </div>
                    </td>
                    <td>
                      <div class="text-center">
                        <p class="mb-0 text-xs font-weight-bold">Categorie</p>
                        <h6 class="mb-0 text-sm">{{entreprise.category }}</h6>
                      </div>
                    </td>
                    
              
               
              </tr>
            </tbody>
          </table>
  
      </div>
        
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        
      </div>
    </div>
  </div>
</div>

<div class="modal fade" id="documentModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="exampleModalLabel">Detail document</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        <span v-html="this.formulaireDocument"></span>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        
      </div>
    </div>
  </div>
</div>






    <div class="py-4 container-fluid">

      
        <div class="row">
          <div class="col mb-lg">
            <!-- line chart -->
            <div class="card z-index-2">
              <gradient-line-chart />
            </div>
            <br>
          </div>
          </div>
            
              <div class="row">
              <div class="col mb-lg">


                <div class="card">
                    <div class="card-header pb-0">
                        <h6>Table des formulaires</h6>
                        
                      </div>
                    <div class="table-responsive p-0">
                        <table class="table align-items-center mb-0">
                          <thead>
                            <th class="justify-content-center text-center">Nom Formulaire</th>
                            <th class="justify-content-center text-center">Date insertion</th>
                            <th class="justify-content-center text-center">Etat Formulaire</th>
                            <th class="justify-content-center text-center">Lettre & Liste</th>
                          </thead>
                          <tbody>
                            <tr v-for="formulaire in paginated" :key="formulaire.id">
                              
                                <td>
                                    <div class="text-center">
                                      
                                      <h6 class="mb-0 text-sm">{{ formulaire.nom_formulaire }}</h6>
                                    </div>
                                  </td>
                                  <td>
                                    <div class="text-center">
                                      
                                      <h6 class="mb-0 text-sm">{{new Date(formulaire.created_at).toLocaleDateString() }}</h6>
                                    </div>
                                  </td>
                                  <td class="justify-content-center text-center"><span class="badge bg-gradient-warning mx-1 cursor-pointer" v-if="formulaire.Etat===0">En attente</span>
                                    <span class="badge bg-gradient-danger mx-1 cursor-pointer" v-if="formulaire.Etat===1">En cours</span>
                                    <span class="badge bg-gradient-success mx-1 cursor-pointer" v-if="formulaire.Etat===2">Rejeter</span>
                                    <span class="badge bg-gradient-success mx-1 cursor-pointer" v-if="formulaire.Etat===3">Terminer</span>
                                    
                                  </td>
                                  <td class="justify-content-center text-center"><span class="badge bg-gradient-info mx-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#listModal" @click="getFormulaireList(formulaire.id_liste_entreprise)">Voir liste</span>
                                    <span class="badge bg-gradient-secondary mx-1 cursor-pointer" data-bs-toggle="modal" data-bs-target="#documentModal" @click="getFormulaireDocument(formulaire.id)" v-if="formulaire.type_save===0">Voir la lettre</span>
                                    <a target="_blank" :href="'https://prospect-api.konfiant.com/storage/'+formulaire.dc_path" v-else-if="formulaire.type_save===1"><span class="badge bg-gradient-info mx-1 cursor-pointer" >Tlcharger la lettre</span></a>
                                  </td>
                            
                             
                            </tr>
                          </tbody>
                        </table>
                        <nav aria-label="Page navigation-sm">
                          <ul class="pagination pagination-sm my-2 mx-2">
                            <li class="page-item cursor-pointer" @click="prev">
                              <a class="page-link" aria-label="Previous">
                                <span aria-hidden="true">&laquo;</span>
                              </a>
                            </li>
                            <li :class="this.current==index ? 'page-item active mx-1 cursor-pointer' : 'page-item mx-1 cursor-pointer'" v-for="index in pageCount" :key="index" @click="currentPage(index)"><a class='page-link'  disabled>{{index}}</a></li>
                            
                            <li class="page-item cursor-pointer" @click="next()">
                              <a class="page-link" aria-label="Next" >
                                <span aria-hidden="true">&raquo;</span>
                              </a>
                            </li>
                          </ul>
                        </nav>
                    </div>
                      </div>
                
              </div>


        </div>
    
        
        
        
        
      </div>
</template>