<script>
import axios from 'axios'
import ArgonInput from "@/components/ArgonInput.vue";
import ArgonButton from "@/components/ArgonButton.vue";
import ArgonSwitch from "@/components/ArgonSwitch.vue";
export default {
    name:'user',
    components:{
        ArgonInput,
        ArgonButton,
        ArgonSwitch
        
    },
    data(){
        return {
            user:[],
            inputsState:true,
            confirmStatus:0,
            allTypeEntreprise:[],
        }
    },

    methods:{
        getCurrentUser(){
            axios.defaults.withCredentials = true
            axios.get('https://prospect-api.konfiant.com/api/getCurrentUser').then((response)=>{
                console.log(response);
                this.user=response.data
            })
            .catch((error)=>{
                console.log(error);
            })

        },

        getAllTypesEntreprise(){
          axios.get('https://prospect-api.konfiant.com/api/getAllTypesEntreprise').then((response)=>{
            console.log(response.data);
            this.allTypeEntreprise=response.data;
          })
          .catch((error)=>{
            console.log(error);
          })

        },

        updateUser(){

            axios.post('https://prospect-api.konfiant.com/api/updateUser',
            {   name:this.user.name,
                prenom:this.user.prenom,
                email:this.user.email,
                adresse:this.user.adresse,
                city:this.user.city,
                country:this.user.country,
                zip:this.user.zip,
                entreprise:this.user.entreprise,
                type_entreprise:this.user.type_entreprise,
                id:this.user.id,
                siren:this.user.siren
                
            }
            ).then((response)=>{
                console.log(response);
                this.getCurrentUser();
                this.inputsState=true;
                this.confirmStatus=response.status
                

            }).catch((error)=>{
                console.log(error);
            })
        },
        isAuthenticated(){
          axios.defaults.withCredentials = true
        axios.get("https://prospect-api.konfiant.com/api/isAuthenticated").then((response)=>{
      console.log(response);
      
    })
    .catch((error)=>{
      console.log(error);
      this.$router.replace(this.$route.query.redirect || '/signin');
    })
    },
        
        
    },

    mounted(){
        this.isAuthenticated();
        this.getAllTypesEntreprise();
        this.getCurrentUser();
        this.inputsState=true;
        
    }
}
</script>

<template>

    <div class="py-4 container-fluid " >
        <div class="row justify-content-center">
          <div class="col-md-10" style="margin-left:20px">
            <div class="card">
              <div class="card-header pb-0">
                <div class="d-flex align-items-center">
                  <p class="mb-0">Edit Profile</p>
                
                  <argon-switch  class="ms-auto" id="rememberMe" @change="this.inputsState=!this.inputsState" :checked="this.inputsState">Modifier mon profile</argon-switch>
                  
                </div>
              </div>
              <div class="card-body">
                <p class="text-uppercase text-sm">User Information</p>
                <div class="alert alert-success w-50 m-auto my-2" v-if="this.confirmStatus===200" role="alert">
                  Modification enregistrer avec succes
                </div>
                <div class="row">
                  
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Email address</label
                    >
                    <argon-input type="email" v-model="this.user.email" :disabled="!this.inputsState" />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Siren</label
                    >
                    <argon-input type="email" v-model="this.user.siren" :disabled="!this.inputsState" />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >First name</label
                    >
                    <argon-input type="text" v-model="this.user.prenom" :disabled="!this.inputsState" ></argon-input>
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label"
                      >Last name</label
                    >
                    <argon-input type="text" v-model="this.user.name" :disabled="!this.inputsState" />
                  </div>
                </div>
                <hr class="horizontal dark" />
                <p class="text-uppercase text-sm">Info entreprise</p>
                <div class="row">
                    <div class="col-md-6">
                        <label for="example-text-input" class="form-control-label"
                          >Entreprise</label
                        >
                        <argon-input  type="text" v-model="this.user.entreprise" :disabled="!this.inputsState" />
                      </div>
                      <div class="col-md-6">
                        <label for="example-text-input" class="form-control-label"
                          >Type entreprise</label
                        >
                        <select class="form-select" aria-label="Default select example" v-model="this.user.type_entreprise" :disabled="!this.inputsState">
                          <option v-for="TypeE in this.allTypeEntreprise" :key="TypeE.id" :value="TypeE.id">{{ TypeE.libelle_type }}</option>
                          </select>
                      </div>
                </div>
                <hr class="horizontal dark" />
                <p class="text-uppercase text-sm">Contact Information</p>
                <div class="row">
                  <div class="col-md-12">
                    <label for="example-text-input" class="form-control-label"
                      >Address</label
                    >
                    <argon-input
                      type="text"
                      v-model="this.user.adresse" :disabled="!this.inputsState"
                    />
                  </div>
                  <div class="col-md-4">
                    <label for="example-text-input" class="form-control-label"
                      >City</label
                    >
                    <argon-input type="text" v-model="this.user.city" :disabled="!this.inputsState" />
                  </div>
                  <div class="col-md-4">
                    <label for="example-text-input" class="form-control-label"
                      >Country</label
                    >
                    <argon-input type="text" v-model="this.user.country" :disabled="!this.inputsState" />
                  </div>
                  <div class="col-md-4">
                    <label for="example-text-input" class="form-control-label"
                      >Postal code</label
                    >
                    <argon-input type="text" v-model="this.user.zip" :disabled="!this.inputsState" />
                  </div>
                </div>
                
                <argon-button color="success" size="sm" class="ms-auto"
                    @click="updateUser()" :disabled="this.inputsState">Sauvegarder</argon-button
                  >
              </div>
            </div>
          </div>
          <div class="col-md-4">
            <profile-card />
          </div>
        </div>
      </div>
</template>